
export function fetchAllProducts() {
  return new Promise(async (resolve, reject) => {
    const response = await fetch("/product", {
      method: "GET",
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();

    if (response.ok) {
      resolve({ data });
    } else {
      const error = await response.json();
      reject(error);
    }
  });
}

export function fetchProductById(id) {
  return new Promise(async (resolve) => {
    //console.log(id);
    const response = await fetch("/product/" + id);
    const data = await response.json();
    console.log(data);
    resolve({ data });
  });
}

export function createProduct(product) {
  return new Promise(async (resolve) => {
    //console.log(id);
    const response = await fetch("/product/", {
      method: "POST",
      body: JSON.stringify(product),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    console.log(data);
    resolve({ data });
  });
}

export function updateProduct(update) {
  return new Promise(async (resolve) => {
    const response = await fetch("/product/" + update.id, {
      method: "PATCH",
      body: JSON.stringify(update),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchProductsByFilters(filter, sort, pagination) {
  //filter = category: ['groceries', 'essence']
  //sort = {_sort:"price", _order="desc"}
  //pagination = {_page1,_limit=10}
  //console.log(pagination)
  let queryString = "";

  for (let key in filter) {
    const categoryValues = filter[key]; //categoria
    if (categoryValues.length) {
      queryString += `${key}=${categoryValues}&`; //agrego nuevo filtro category=calvinklein&
    }
  }
  for (let key in sort) {
    queryString += `${key}=${sort[key]}&`;
  }
  for (let key in pagination) {
    //console.log(pagination[key]);
    queryString += `${key}=${pagination[key]}&`;
  }

  console.log(queryString);



  return new Promise(async (resolve, reject) => {
    const response = await fetch(
      "/product?" + queryString,
      {
        method: "GET",
        credentials: "include",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      }
    );
    const data = await response.json(); //trae los datos desde la db
    if (response.ok) {
        const totalItems = await response.headers.get("X-Total-Count");
    
    resolve({ data: { products: data, totalItems: +totalItems } }); //data = payload
      } else {
        const error = await response.json();
      reject(error);
      }


    
  });
}

