import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { createUser, checkUser, SignOut, checkAuth } from "./authAPI";

const initialState = {
  loggedInUserToken: null, //esta propiedad solo contendra la identidad del usuario =>'id' , 'role'
  status: "idle",
  error: null,
  userChecked : false,
  userRegistered:false
};

export const createUserAsync = createAsyncThunk(
  "user/createUser",
  async (userData , { rejectWithValue }) => {
    try {
    const response = await createUser(userData); //envio datos al api
    return response.data;
  } catch (error) {
    console.log(error);
    return rejectWithValue(error);
  }
});

export const checkUserAsync = createAsyncThunk(
  //checkeo si el signup es valido
  "user/checkUser",
  async (loginInfo, { rejectWithValue }) => {
    try {
      const response = await checkUser(loginInfo);
      return response.data;
    } catch (error) {
      console.log(error);
      return rejectWithValue(error);
    }
  }
);

export const signOutAsync = createAsyncThunk(
  "user/signOut",
  async () => {
    const response = await SignOut();
    console.log(response.data)
    return response.data;
  }
);

export const checkAuthAsync = createAsyncThunk(
  "user/checkAuth",

  async ( ) => {
    try {
      const response = await checkAuth();
      console.log(response.data)
      return response.data;
     
    } catch (error) {
      console.log(error);
    }
  }
);

export const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    increment: (state) => {
      //tomo el estado actual
      state.value += 1; //paso al siguiente estado
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload; //guardo nuevo estado
      })
      .addCase(createUserAsync.rejected, (state, action) => {
        state.status = "idle";
        state.error = action.payload; //guardo nuevo estado
      })
      .addCase(checkUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(checkUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload; //checkeo en el login y guardo datos de la respeusta del back
      })
      .addCase(checkUserAsync.rejected, (state, action) => {
        state.status = "idle";
        state.error = action.payload; //guardo nuevo estado
      })

      .addCase(signOutAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(signOutAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = null; //guardo nuevo estado
        
    })
      .addCase(checkAuthAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(checkAuthAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload; //guardo nuevo estado
        state.userChecked = true
      })
      .addCase(checkAuthAsync.rejected, (state, action) => {
        state.status = "idle";
        state.userChecked = true; //guardo nuevo estado
      });
  },
});

export const selectLoggedInUser = (state) => state.auth.loggedInUserToken; //obtengo datos del usuario logeado

export const selectError = (state) => state.auth.error;
export const selectUserChecked = (state) => state.auth.userChecked
//export const selectUserRegistered = (state) => state.auth.userRegistered
export const { increment } = authSlice.actions; //??
export default authSlice.reducer;
