import "./App.css";
import { BrowserRouter, Routes, Route, useNavigate } from "react-router-dom";


import ProfilePage from "./pages/ProfilePage.jsx";

import Home from "./pages/Home.jsx";
import CartPage from "./pages/CartPage.jsx";
import Checkout from "./pages/Checkout.jsx";
import ProductDetailPage from "./pages/ProductDetailPage.jsx"
import LoginPage from "./pages/LoginPage.jsx";
import SignupPage from "./pages/SignupPage.jsx";
import Protected from './auth/components/Protected.js';
import { useDispatch,useSelector } from "react-redux";
import { useEffect } from "react";
import { fetchItemsByUserIdAsync } from "./cart/CartSlice.js";
import {selectLoggedInUser,checkAuthAsync,selectUserChecked } from './auth/authSlice.js';
import  NotFoundPage from './pages/NotFoundPage.jsx';
import OrderSuccessPage from "./pages/OrderSuccessPage.jsx";
import UserOrdersPage from "./pages/UserOrdersPage.jsx";
import { fetchLoggedInUserAsync} from "./user/userSlice.js";
import Logout from "./auth/components/Logout.js";
import ForgotPasswordPage from "./pages/ForgotPasswordPage.jsx";
import ProtectedAdmin from './auth/components/ProtectedAdmin.js'
import AdminHome from "./pages/AdminHome.jsx";
import AdminProductDetailPage from './pages/AdminProductDetailPage.jsx'
import AdminProductFormPage from './pages/AdminProductFormPage.jsx'
import AdminOrdersPage from "./pages/AdminOrdersPage.jsx";
import HomePage from "./pages/Homepage.jsx";

function App() {
const dispatch = useDispatch();
const user = useSelector(selectLoggedInUser);
const userChecked = useSelector(selectUserChecked)

console.log(user)

useEffect(()=>{
 dispatch(checkAuthAsync())
 
  },[])

useEffect(() =>{
  if(user!==null){
    dispatch(fetchItemsByUserIdAsync());//cargo los items del usuario para mostrarlo en navbar
    dispatch(fetchLoggedInUserAsync());//cargo los datos del usuario logeado 
  }

},[dispatch,user])

  //fragment <> se utiliza para escribir codigo html en js
  return (
    <>
    <div>
      {userChecked &&
  <BrowserRouter>
      <Routes>
      <Route path="/cart" element={<Protected><CartPage/></Protected>} />
      <Route path="/login" element={<LoginPage/>} />
      <Route path="/sign-up" element={<SignupPage/>} />
      
      
        <Route path="/" element={<HomePage />} />
        <Route path="admin/home" element={<ProtectedAdmin><AdminHome /></ProtectedAdmin>} />
        <Route path="/homeshop" element={<Protected><Home/></Protected>} />
        <Route path="/checkout" element={<Protected><Checkout/></Protected>} />
        <Route path="/product-detail/:id" element={<Protected><ProductDetailPage/></Protected>} />
        <Route path="admin/product-detail/:id" element={<ProtectedAdmin><AdminProductDetailPage/></ProtectedAdmin>} />
        <Route path="admin/product-form" element={<ProtectedAdmin><AdminProductFormPage/></ProtectedAdmin>} />
        <Route path="admin/product-form/edit/:id" element={<ProtectedAdmin><AdminProductFormPage/></ProtectedAdmin>} />
        <Route path="admin/orders" element={<ProtectedAdmin><AdminOrdersPage/></ProtectedAdmin>} />

        <Route path="/order-success/:id" element={<Protected><OrderSuccessPage/></Protected>} />
        <Route path="/orders" element={<Protected><UserOrdersPage/></Protected>} />
        <Route path="/profile" element={<Protected><ProfilePage/></Protected>} />
        <Route path="/logout" element={<Protected><Logout/></Protected>} />
        <Route path="/forgot-password" element={<ForgotPasswordPage/>} />
        <Route path="*" element={<NotFoundPage></NotFoundPage>} />
      </Routes>
    </BrowserRouter>}
    </div></>

  );
}

export default App;
