import { useForm } from "react-hook-form";
import { useSelector, useDispatch } from "react-redux";
import { selectUserInfo, updateUserAsync } from "../userSlice";
import { useState } from "react";
import toast, { Toaster } from 'react-hot-toast';

export default function UserProfile() {
  const dispatch = useDispatch();
  const userInfo = useSelector(selectUserInfo);
  console.log(userInfo)
  const [selectedEditIndex,setSelectedEditIndex]= useState(-1);
  const {
    register,
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm();

  const handleEdit = (addressUpdate, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
    newUser.addresses.splice(index, 1, addressUpdate); //elimino del arrelgo el index
    dispatch(updateUserAsync(newUser));
    setSelectedEditIndex(-1);
    toast.success('Direccion editada');
  };

  const handleDelete = (e, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
    newUser.addresses.splice(index, 1); //elimino del arrelgo el index
    dispatch(updateUserAsync(newUser)); //actualizo el neuvo usuario
    toast.success('Direccion eliminada');
  };

  const handleEditClick=(index) =>{
    setSelectedEditIndex(index);
    const address = userInfo.addresses[index]
    setValue('name',address.name);
    setValue('email',address.email)
    setValue('phone',address.phone)
    setValue('street',address.street)
    setValue('city',address.city)
    setValue('state',address.state)
    setValue('pinCode',address.pinCode)

  }

  return (
    <>
    <Toaster/>
  <div>
  <div className="mx-auto mt-6 bg-white max-w-7xl px-4 sm:px-6 lg:px-8">
    <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
      <h1 className="text-4xl my-5 font-bold tracking-tight text-blue-500">
        Correo Electronico: {userInfo.email}
      </h1>
      {userInfo.role === 'admin' && (
        <h1 className="text-4xl my-5 font-bold tracking-tight text-blue-500">
          Role: {userInfo.role}
        </h1>
      )}
      {userInfo.role === 'user' && (
        <h1 className="text-4xl my-5 font-bold tracking-tight text-blue-500">
          Role: {userInfo.role}
        </h1>
      )}
    </div>

    <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
      {userInfo.addresses.map(function (address, index) {
        return (
          <div key={index}>
            {selectedEditIndex === index ? (
              <form
                className="bg-white px-5 mt-12"
                noValidate
                onSubmit={handleSubmit((data) => {
                  handleEdit(data, index);
                  reset();
                })}
              >
                <div className="space-y-12">
                  <div className="border-b border-gray-900/10 pb-12 py-6">
                    <h2 className="text-2xl font-semibold leading-7 text-gray-900">
                      Informacion Personal
                    </h2>
                    <p className="mt-1 text-sm leading-6 text-gray-600">
                      Utilize un email real.
                    </p>

                    <div className="mt-10 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-2 gap-x-6 gap-y-8">
                      {/* Form fields */}
                      <div className="col-span-full">
                        <label
                          htmlFor="name"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Nombre completo
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('name', {
                              required: 'Nombre es requerido',
                            })}
                            id="name"
                            autoComplete="name"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="col-span-full">
                        <label
                          htmlFor="email"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Correo electronico
                        </label>
                        <div className="mt-2">
                          <input
                            id="email"
                            {...register('email', {
                              required: 'Email es requerido',
                            })}
                            type="email"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="col-span-full sm:col-span-1">
                        <label
                          htmlFor="phone"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Telefono
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('phone', {
                              required: 'Telefono es requerido',
                            })}
                            id="phone"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="col-span-full">
                        <label
                          htmlFor="street"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Direccion
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('street', {
                              required: 'Direccion es requerida',
                            })}
                            id="street"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-1">
                        <label
                          htmlFor="city"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Ciudad
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('city', {
                              required: 'Ciudad es requerido',
                            })}
                            id="city"
                            autoComplete="address-level2"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-1">
                        <label
                          htmlFor="state"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Estado / Provincia
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('state', {
                              required: 'Estado es requerido',
                            })}
                            id="state"
                            autoComplete="address-level1"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      <div className="sm:col-span-1">
                        <label
                          htmlFor="pinCode"
                          className="block text-sm font-medium leading-6 text-gray-900"
                        >
                          Codigo postal
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('pinCode', {
                              required: 'Codigo pin es requerido',
                            })}
                            id="pinCode"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="mt-6 flex items-center justify-end gap-x-6">
                    <button
                      onClick={(e) => setSelectedEditIndex(-1)}
                      type="button"
                      className="rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-red-600"
                    >
                      Cancelar
                    </button>

                    <button
                      type="submit"
                      className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    >
                      Editar direccion
                    </button>
                  </div>
                </div>
              </form>
            ) : null}
            <p className="mt-5 text-sm text-gray-500">Direcciones almacenadas:</p>
            <div className="flex flex-wrap gap-6">
            <div className="w-full sm:w-1/2 lg:w-full flex justify-between border-t border-gray-200 gap-x-6 py-3 my-2">
  <div className="flex min-w-0 flex-col gap-x-4"> {/* Ajustado para el contenedor principal */}
    <div className="min-w-0 flex-grow"> {/* Ajustado para el contenido */}
      <p className="text-sm font-semibold leading-6 text-gray-900">
        Nombre: {address.name}
      </p>
      <p className="mt-1 truncate text-xs leading-5 text-gray-500">
        Direccion: {address.street}
      </p>
      <p className="mt-1 truncate text-xs leading-5 text-gray-500">
        Ciudad: {address.city}
      </p>
      <p className="mt-1 truncate text-xs leading-5 text-gray-500">
        Estado: {address.state}
      </p>
      <p className="mt-1 truncate text-xs leading-5 text-gray-500">
        Codigo postal: {address.pinCode}
      </p>
    </div>
  </div>
  <div className="flex flex-col justify-end items-end ml-auto"> {/* Ajustado para alinear los botones a la derecha */}
    <button
      onClick={() => handleEditClick(index)}
      className="mb-2 rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
    >
      Editar
    </button>
    <button
      onClick={() => handleDelete(address.id)}
      className="rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-red-600"
    >
      Eliminar
    </button>
  </div>
</div>

            </div>
          </div>
        );
      })}
    </div>
  </div>
</div>

    </>
  );
}
