import { useEffect, useState, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  fetchAllOrdersAsync,
  selectOrders,
  selectTotalOrders,
  updateOrderAsync,
} from "../../order/orderSlice";
import { PencilIcon } from "@heroicons/react/24/outline";
import { ITEMS_PER_PAGE, discountedPrice } from "../../helper/constants";

function AdminOrders() {
  const [page, setPage] = useState(1);
  const dispatch = useDispatch();
  const orders = useSelector(selectOrders);
  const totalOrders = useSelector(selectTotalOrders);
  const [editableOrderId, setEditableOrderId] = useState(null);

  const fetchOrders = useCallback(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    dispatch(fetchAllOrdersAsync(pagination));
  }, [dispatch, page]);

  useEffect(() => {
    fetchOrders();
  }, [fetchOrders]);

  const handleEdit = (order) => {
    setEditableOrderId(order.id);
  };

  const handleUpdate = (e, order) => {
    const updatedOrder = { ...order, status: e.target.value };
    dispatch(updateOrderAsync(updatedOrder))
      .then(() => setEditableOrderId(null))
      .catch(error => console.error("Failed to update order:", error));
  };

  const chooseColor = useCallback((status) => {
    switch (status) {
      case "pending":
        return "bg-purple-200 text-purple-600";
      case "despachado":
        return "bg-yellow-200 text-yellow-600";
      case "entregado":
        return "bg-green-200 text-green-600";
      case "cancelado":
        return "bg-red-200 text-red-600";
      default:
        return "bg-purple-200 text-purple-600";
    }
  }, []);

  const handlePageChange = (newPage) => {
    if (newPage > 0 && newPage <= Math.ceil(totalOrders / ITEMS_PER_PAGE)) {
      setPage(newPage);
    }
  };

  return (
    <div className="overflow-x-auto">
      <div className="bg-gray-100 flex items-center justify-center font-sans overflow-hidden">
        <div className="w-full">
          <div className="bg-white shadow-md rounded my-6">
            <div className="overflow-x-auto">
              <table className="w-full table-auto min-w-[600px]"> {/* Establecer un ancho mínimo */}
                <thead>
                  <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                    <th className="py-3 px-6 text-left">#Transaccion</th>
                    <th className="py-3 px-6 text-left min-w-[120px]">Items</th>
                    <th className="py-3 px-6 text-center">Total</th>
                    <th className="py-3 px-6 text-center">Direccion de envio</th>
                    <th className="py-3 px-6 text-center">Estado</th>
                    <th className="py-3 px-6 text-center">Acciones</th>
                  </tr>
                </thead>
                <tbody className="text-gray-600 text-sm font-light">
                  {orders.map((order) => (
                    <tr
                      key={order.id}
                      className="border-b border-gray-200 hover:bg-gray-100"
                    >
                      <td className="py-3 text-left whitespace-nowrap">
                        <div className="flex items-center">
                          <span className="font-medium">{order.id}</span>
                        </div>
                      </td>
                      <td className="py-3 px-6 text-left">
                        {order.items.map((item) => (
                          <div key={item.product.id} className="flex items-center">
                            <img
                              className="w-6 h-6 rounded-full mr-2"
                              src={item.product.thumbnail}
                              alt={item.product.title}
                            />
                            <span>
                              {item.product.title} - #{item.quantity} - $
                              {discountedPrice(item.product)}
                            </span>
                          </div>
                        ))}
                      </td>
                      <td className="py-3 px-6 text-center">
                        ${order.totalAmount}
                      </td>
                      <td className="py-3 px-6 text-center">
                        <div>
                          <div><strong>{order.selectedAddress.name}</strong></div>
                          <div>{order.selectedAddress.street}</div>
                          <div>{order.selectedAddress.city}</div>
                          <div>{order.selectedAddress.state}</div>
                          <div>{order.selectedAddress.pinCode}</div>
                          <div>{order.selectedAddress.phone}</div>
                        </div>
                      </td>
                      <td className="py-3 px-6 text-center">
                        {order.id === editableOrderId ? (
                          <select value={order.status} onChange={(e) => handleUpdate(e, order)}>
                            <option value="pending">Pendiente</option>
                            <option value="despachado">Despachado</option>
                            <option value="entregado">Entregado</option>
                            <option value="cancelado">Cancelado</option>
                          </select>
                        ) : (
                          <span className={`${chooseColor(order.status)} py-1 px-3 rounded-full text-xs`}>
                            {order.status}
                          </span>
                        )}
                      </td>
                      <td className="py-3 px-2 text-center">
                        <div className="flex item-center justify-center">
                          <div className="cursor-pointer w-4 mr-2 transform hover:text-purple-500 hover:scale-110">
                            <PencilIcon
                              className="w-6 h-6"
                              onClick={() => handleEdit(order)}
                            />
                          </div>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
           {/*  <div className="flex justify-between items-center p-4">
              <button
                onClick={() => handlePageChange(page - 1)}
                disabled={page <= 1}
                className="bg-gray-300 text-gray-800 py-1 px-4 rounded disabled:opacity-50"
              >
                Previous
              </button>
              <span>Page {page}</span>
              <button
                onClick={() => handlePageChange(page + 1)}
                disabled={page >= Math.ceil(totalOrders / ITEMS_PER_PAGE)}
                className="bg-gray-300 text-gray-800 py-1 px-4 rounded disabled:opacity-50"
              >
                Next
              </button>
            </div> */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default AdminOrders;
