import { Link,Navigate } from "react-router-dom"
import {useForm} from 'react-hook-form';
import { createUserAsync,selectLoggedInUser,selectError } from "../authSlice";
import {useSelector,useDispatch} from 'react-redux';


export default function Signup(){
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);
  const error = useSelector(selectError);
   const{register,handleSubmit,formState:{errors}}= useForm();
    console.log(error);
   
    return(<>
{user && <Navigate to='/login' replace={true}></Navigate>}
<div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8 bg-white">
        <div className="sm:mx-auto sm:w-full sm:max-w-sm">
          <img
            className="mx-auto h-20 w-auto"
            src="http://www.yoimplemento.cl/sitio/wp-content/uploads/2021/02/cropped-cropped-cropped-LOGOTIPO-YO-IMPLEMENTO.png"
            
            alt="Your Company"
          />
          <h2 className="mt-10 text-center text-xl font-bold leading-9 tracking-tight text-gray-900">
            Crear nueva cuenta
          </h2>
        </div>

        <div className="mt-4 sm:mx-auto sm:w-full sm:max-w-sm">
          <form noValidate className="space-y-6" onSubmit={handleSubmit((data) =>{
            dispatch(createUserAsync({email:data.email,password:data.password,addresses:[]}))
            console.log(data)
          })}>
            <div>
              <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                Correo Electronico
              </label>
              <div className="mt-2">
                <input
                  id="email"
                  {...register("email",{required:"Correo electronico es requerido",pattern:{value:/\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/gi,message:"Email invalido"}})}
                  type="email"
                  autoComplete="email"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                {errors.email && <p className="text-red-500">{errors.email.message}</p>}
              </div>
            </div>

            <div>
              <div className="flex items-center justify-between">
                <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900">
                  Contraseña
                </label>
                <div className="text-sm">
                  
                </div>
              </div>
              <div className="mt-2">
                <input
                  id="password"
                  {...register("password",{required:"Contraseña requerida",pattern:{value:/^(?=.*?[0-9])(?=.*?[#?!@$%^&*-])/,message:`-Contraseña debe contener numero \n -Contraseña debe contener un caracter especial`}})}
                  type="password"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                {errors.password && <p className="text-red-500">{errors.password.message}</p>}
              </div>
            </div>
            <div>
              <div className="flex items-center justify-between">
                <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900">
                 Confirmar contraseña
                </label>
                
              </div>
              <div className="mt-2">
                <input
                  id="confirmPassword"
                  {...register("confirmPassword",{required:"Confirmar contraseña es requerido",validate:(value,formValues) => value === formValues.password || 'Contraseña no son iguales'})}
                  type="password"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                {errors.confirmPassword &&<p className="text-red-500">{errors.confirmPassword.message}</p>}
                {errors.password && <p className="text-red-500">{errors.password.message}</p>}
                  {error && <p className="text-red-500">{ error.msg}</p>}
              </div>
            </div>
            <div>
              <button
                type="submit"
                className="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Crear Cuenta
              </button>
            </div>
          </form>

          <p className="mt-10 text-center text-sm text-gray-500">
            Ya es miembro?{' '}
            <Link to="/login" className="font-semibold leading-6 text-indigo-600 hover:text-indigo-500">
              Identificarse
            </Link>
          </p>
        </div>
      </div>

      </>
    )
}